"use client";

import { useState } from "react";
import { Button } from "@/shared/ui/button";
import { Input } from "@/shared/ui/input";
import { Textarea } from "@/shared/ui/textarea";
import { TeamProjectsService } from "../api/team-projects.service";
import { useToast } from "@/shared/ui/use-toast";

interface CreateProjectProps {
  teamId: number;
  onProjectCreated: () => void;
}

export function CreateProject({
  teamId,
  onProjectCreated,
}: CreateProjectProps) {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!name.trim()) {
      toast({
        title: "Ошибка",
        description: "Название проекта обязательно",
        variant: "destructive",
      });
      return;
    }

    try {
      setIsLoading(true);
      await TeamProjectsService.createProject(teamId, {
        name: name.trim(),
        description: description.trim() || null,
      });
      toast({
        title: "Успешно",
        description: "Проект создан",
      });
      onProjectCreated();
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось создать проект",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-2xl mx-auto">
      <div className="bg-white p-6 rounded-lg border">
        <h2 className="text-xl font-bold mb-4">Создание дипломного проекта</h2>
        <p className="text-gray-600 mb-6">
          Создайте проект для вашей дипломной работы. После создания проекта вы
          сможете загружать файлы и отслеживать прогресс по этапам.
        </p>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="name" className="block font-medium mb-1">
              Название проекта
            </label>
            <Input
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="Введите название проекта"
              disabled={isLoading}
            />
          </div>

          <div>
            <label htmlFor="description" className="block font-medium mb-1">
              Описание
            </label>
            <Textarea
              id="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="Опишите ваш проект"
              disabled={isLoading}
            />
          </div>

          <Button type="submit" disabled={isLoading}>
            {isLoading ? "Создание..." : "Создать проект"}
          </Button>
        </form>
      </div>
    </div>
  );
}
